  float: left;
  position: relative;
  width: 30%;
  font-size: 9px;
}

ul#progressive-bar li:before {
  content: counter(step);
  counter-increment: step;
  width: 20px;
  line-height: 20px;
  display: block;
  font-size: 10px;
  color: #333;
  background: white;
  border-radius: 3px;
  margin: 0 auto 5px auto;
  border: 1px solid #e8b617;
}

